import Popup from './Popup'

function PopupWithThanks({
  isOpen,
  onClosePopup
}) {

  return (
    <Popup
      isOpen={isOpen}
      onClosePopup={onClosePopup}
    >
      <h2 className="popup__thanks-title">
           !
      </h2>
      <p className="popup__thanks-text">
               
      </p>
    </Popup>
  )
}

export default PopupWithThanks
