import OrderForm from './OrderForm'
import SocialIcon from './SocialIcon'

import { COLOR_WHITE, socials } from '../utils/constants'

function Footer({
  onOrderSubmit,
  isLaptop
}) {

  return (
    <footer className="footer">
      <div className="footer__wrapper">
        <h2 className="footer__title"> ?    </h2>
        <div className="footer__form-block">
          <OrderForm
            inputTextColor={COLOR_WHITE}
            onOrderSubmit={onOrderSubmit}
            btnText=" "
          />
        </div>
        {isLaptop ?
          <div className="footer__tablet" />
        :
          <div className="footer__address">
            <p className="footer__address-text">8 800 800 80 80</p>
            <p className="footer__address-text">:  ,  1</p>
            <p className="footer__address-text"> :</p>
            <ul style={{ listStyle: 'none', margin: '0' }}>
              <li className="footer__address-text">- 10:00-20:00</li>
              <li className="footer__address-text">- </li>
            </ul>
          </div>
        }
        <p className="footer__text">
            ,        !
        </p>
        {!isLaptop && <div className="footer__links">
          {socials.map((link) => {
            return <SocialIcon
              key={link.name}
              name={link.name}
              href={link.href}
            />
          })}
        </div>}
      </div>
    </footer>
  )
}

export default Footer
